/* Nav bar & banner */

header {
  color: white;
  background-image: -webkit-gradient(linear, left top, right top, from(rgba(0,0,0,0.8)), to(rgba(255,255,255,1)));
  background-image: -webkit-linear-gradient(left, rgba(0,0,0,0.8), rgba(255,255,255,1));
  background-image: -o-linear-gradient(left, rgba(0,0,0,0.8), rgba(255,255,255,1));
  background-image: linear-gradient(to right, rgba(0,0,0,0.8), rgba(255,255,255,1));
}

header img {
  width: 100%;
}

nav {
  position: fixed;
  width: 100%;
  height: 50px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: end;
      justify-content: flex-end;
  -ms-flex-align: center;
      align-items: center;
  background-color: rgba(0, 0 , 0, .8);
}

nav a {
  text-decoration: none;
  color: rgba(255, 255, 255, 1);
  padding: 10px;
  margin: 5px;
}

nav a:hover {
  background-color: rgba(255,255,255,0.9);
  color: rgba(0, 0 , 0, 0.9);
  -webkit-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
}

section {
  text-align: center;
  margin: 25px auto;
}

hr {
  width: 90%;
}

.about, .dev-blog, .project {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  margin: 25px 50px;
}

iframe {
  width: 560px;
  height: 315px;
}

.img-wrapper, .desc-text-wrapper {
  margin: auto 25px;
  max-width: 400px;
  max-height: 400px;
}

.desc-text-wrapper a {
  color: black;
}

.desc-text-wrapper a:hover {
  color: black;
  text-decoration: underline;
  -webkit-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
}

.img-wrapper:hover img {
  opacity: 0.5;
  -webkit-transition: 0.5s ease;
  -o-transition: 0.5s ease;
  transition: 0.5s ease;
}

img {
  width: 100%;
  height: auto;
}

.social-links a {
  color: black;
  margin: 10px;
}

.social-links i:hover {
  -webkit-transform: scale(1.2);
  -ms-transform: scale(1.2);
  transform: scale(1.2);
  -webkit-transition: 1s;
  -webkit-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
}


@media only screen and (max-width: 800px) {
  .about {
    margin: auto;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
  }
  .dev-blog, .project {
    margin: auto 25px;
  }
  iframe {
    max-width: 95%;
    max-height: 270px;
  }
}

@media only screen and (max-width: 480px) {
 .dev-blog, .project {
    margin: auto;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
  }
  .reverse-col {
    -ms-flex-direction: column-reverse;
        flex-direction: column-reverse;
  }
  .img-wrapper, .desc-text-wrapper {
    width: 95%;
    height: auto;
  }
}



